import React from 'react';
import bg from './112222.png';
import xz from '../../style/img/xz_1.png';

// const zhufu = ['祝', '陈'];
const zhufu = ['祝', '陈', '璇', '璇', '新', '年', '快', '乐'];
export default class index extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isVisible: false,
            itemVisible: true,
            markVisible: true,
            index: 0,
            words: [
                {title: '送你一朵六角雪花', value: 'fadeInRightBig'},
                {title: '一角携着快乐', value: 'fadeInLeftBig'},
                {title: '一角携着幸福', value: 'fadeInRightBig'},
                {title: '一角携着开心', value: 'fadeInLeftBig'},
                {title: '一角携着浪漫', value: 'fadeInRightBig'},
                {title: '一角携着甜蜜', value: 'fadeInLeftBig'},
                {title: '一角携着祝福', value: 'fadeInRightBig'},
                {title: '祝你除夕夜安康', value: 'fadeInLeftBig'},
                {title: '提前祝你春节快乐', value: 'fadeInRightBig'},
                {title: '——宋志宽赠', value: 'fadeInLeftBig'},
            ],
            zhufuWords: []
        }
    }

    componentDidMount() {
        const t = setTimeout(() => {
            this.setState(() => ({
                isVisible: true
            }));
            clearTimeout(t);
        }, 1000);
        const t2 = setInterval(() => {
            this.next();
            if (this.state.index === (zhufu.length - 1)) {
                clearInterval(t2);
                const t3 = setInterval(() => {
                    if (this.state.zhufuWords.length === this.state.words.length) {
                        clearInterval(t3);
                        return;
                    }
                    const {zhufuWords, words} = this.state;
                    zhufuWords.push(words[zhufuWords.length]);
                    this.setState(() => ({
                        zhufuWords,
                        markVisible: false
                    }));
                }, 2000)
            }
        }, 2000)
    }

    next = () => {
        this.setState(() => ({
            itemVisible: false
        }), () => {
            this.setState(() => ({
                itemVisible: true,
                index: this.state.index + 1
            }));
        });
    };
    getZhufuItem = () => {
        console.log(this.state.zhufuWords);
        const {zhufuWords, words} = this.state;
        return zhufuWords.map((v, i) => {
            const isLast = zhufuWords.length === words.length && i === words.length - 1;
            return <div key={v.title}
                        className={`animated ${v.value} trans ${isLast ? 'right-align full-w block relative' : 'center'}`}
                        style={{lineHeight: isLast ? 3 : 2}}>{v.title}</div>
        })
    };
    getItem = () => {
        return <div
            className="absolute top-0 left-0 right-0 bottom-0 m-auto flex flex-column justify-center items-center animated bounceInDown"
            style={{
                width: 200,
                height: 400,
            }}
        >
            <div
            />
            <span className="css175d607ebf1f1d7 " style={{color: "white", fontSize: 40}}>
                {zhufu[this.state.index]}
            </span>
        </div>
    };

    render() {
        const {className} = this.props;
        // const markStyle = {};
        return (
            <div className={'full-w full-h relative ' + className}
                 style={{backgroundImage: `url(${bg})`, backgroundSize: '100% 100%'}}>

                {this.state.isVisible &&
                <div
                    className="full-w full-h relative flex flex-column items-center justify-between css175d607ebf1f1d7 trans"
                    style={{
                        backgroundColor: this.state.markVisible ? `rgba(0,0,0,.4)` : `rgba(0,0,0,0)`,
                        fontSize: 20,
                    }}>
                    {/*<img src={xz} alt="" style={{*/}
                        {/*width: 80,*/}
                        {/*visibility: this.state.markVisible ? "hidden" : "visible",*/}
                        {/*marginTop: '5vh'*/}
                    {/*}}/>*/}
                    <div
                        className="flex-grow-1 relative flex flex-column items-center justify-center css175d607ebf1f1d7 trans"
                        style={{
                            padding: '0 30px'
                        }}
                    >
                        {this.getZhufuItem()}
                    </div>
                    {/*<img src={xz} alt="" style={{width: 80, visibility: this.state.markVisible ? "hidden" : "hidden"}}/>*/}
                    {this.state.itemVisible && this.getItem()}
                </div>
                }
                <style>
                    {
                        `
                            .trans{
                                 -webkit-transition: 1s;/*浏览器前缀，兼容其他浏览器 chrome,safari*/
                                   -moz-transition: 1s;/*firefox*/
                                    -ms-transition: 1s;/*ie*/
                                     -o-transition: 1s;/*opera*/
                                        transition: 1s;
                            }
                        `

                    }
                </style>
            </div>
        )
    }
}
