import React from 'react';
import classNames from 'classnames';
import {isMobile} from "../utils";
import P1 from './p1';
import mp from '../style/img/music_pointer.png'
import md from '../style/img/music_disc.png'
const musics = ["http://47.104.203.64:9527/Olly+Murs-That+Girl.mp3","http://47.104.203.64:9010/hp.mp3"];
export default class index extends React.Component {
    constructor(props){
        super(props);
        this.state={
            playC:'play',
            state1:true,
            state2:false,
            state3:false,
            state4:false,
        };
        this.step = 1;
    }
    music = ()=>{
        if(this.audio){
            if(this.audio.paused){
                this.audio.play();
                    this.setState(()=>({
                        playC:'play'
                    }));
            }else{
                this.audio.pause();
                this.setState(()=>({
                    playC:''
                }));
            }
        }
    };
    setS = (key1,key2)=>{
        let t = setTimeout(()=>{
            let state = {};
            state[key1] = false;
            this.setState(()=>(state));
            clearTimeout(t);
        },2000);
        let state = {};
        state[key2] = true;
        this.setState(()=>(state));
    };
    play = (isCount)=>{
        if(!isCount)return;
        if(this.audio){
            this.audio.play();
        }
        this.step +=1;
        if(this.step === 2){
            this.setS('state1','state2')
        }else if(this.step === 3){
            this.setS('state2','state3')
        }else if(this.step === 4){
            this.setS('state3','state4')
        }
    };
    componentDidMount(){
        this.audio&&this.audio.play()
    }
    render(){
        const settings={
            style:Styles.wrap
            };
        const {playC,state1,state2,state3,state4}=this.state;
        return (
            <div {...settings} className="relative">
                <div className="music"
                     onClick={this.music}
                     // onTouchStart={this.music}
                >
                    <img src={mp}/>
                    <img className={playC} id="music" src={md}/>
                </div>
                {state1 && <div className="absolute top-0 left-0 full-h full-w">
                    <P1 play={this.play} className={classNames({
                        animated:true,
                        fadeOutDown:state2,
                    })}/>
                </div>}

                <audio ref={(com)=>this.audio = com} controls className="foo" preload="auto" src={musics[0]}
                       loop={true}
                       style={{display:'none'}}
                       type="audio/mpeg">
                    ...
                </audio>
                <style>{
                        `
                            .music {
                                position: absolute;
                                top: 3%;
                                right: 4%;
                                z-index: 5;
                                width: 50px;
                                height: 50px;
                                border: 4px solid #ef1639;
                                -webkit-border-radius: 50%;
                                border-radius: 50%;
                                background: #fff
                            }

                            .music > img:first-child {
                                position: absolute;
                                top: 24%;
                                right: 2.5%;
                                width: 28.421%;
                                z-index: 1
                            }

                            .music > img:last-child {
                                position: absolute;
                                top: 0;
                                right: 0;
                                bottom: 0;
                                left: 0;
                                margin: auto;
                                width: 79%;
                                z-index: 0
                            }

                            .music > img.play {
                                -webkit-animation: music_disc 4s linear infinite;
                                animation: music_disc 4s linear infinite
                            }

                            @-webkit-keyframes music_disc {
                                0% {
                                    -webkit-transform: rotate(0deg);
                                    transform: rotate(0deg)
                                }
                                100% {
                                    -webkit-transform: rotate(360deg);
                                    transform: rotate(360deg)
                                }
                            }

                            @keyframes music_disc {
                                0% {
                                    -webkit-transform: rotate(0deg);
                                    transform: rotate(0deg)
                                }
                                100% {
                                    -webkit-transform: rotate(360deg);
                                    transform: rotate(360deg)
                                }
                            }


                        `
                }</style>
            </div>
        )
    }
}
const Styles = {
    wrap:{
        width:isMobile() ? '100vw' : '375px',
        height:isMobile() ? '100vh':'812px',
        backgroundColor:'lightblue'
    }
};